@import '../../global.scss';

.testimonial{
    background-color:white;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mobile{
        justify-content:space-around ;
    }

   h1{
       font-size: 50px;


       @include mobile{
        font-size:20px;
     }
   }
   .container{
       width: 100%;
       height:80%;
       display: flex;
       align-items: center;
       justify-content: center;

       @include mobile{
        flex-direction :column;
        justify-content:space-between;
        // height : 100%;
        }

       .card{
           width: 250px;
           height: 70%;
           border-radius:10px;
           box-shadow: 0px 0px 15px -8px black;
           display : flex;
           flex-direction: column;
           justify-content:space-around;
           padding-top:20px;
          
           @include mobile{
            height: 180px;
            }

           &:hover{
               transform :scale(1.01)
           }

           .top{
               display: flex;
               align-items:center;
               justify-content:center;

               img{
                   &.left{
                       height:25px
                   }
                   &.user{
                    // height:60px;
                    // width:60px;
                       border-radius: 50%;
                       object-fit:cover;
                       margin : 0 30px;

                       
                    // @include mobile{
                    //     width: 30px;
                    //     height: 30px;
                    //     }
               
                   }
               }
           }
           .center{
               padding :10px;
               margin :10px;
               border-radius: 10px;
               background-color :rgb(231, 211, 215);
           
               @include mobile{
                font-size: 12px;
                padding :5px;
                }
            }
           .bottom{
               display :flex;
               align-items:center;
               justify-content:center;
               flex-direction:column;

               h3{
                    margin-bottom:5px;
                    font-size: 16px;

                    @include mobile{
                        font-size: 14px;
                        }
               }
               h4{
                   color: gray;
                   font-size: 14px;
                   
                   @include mobile{
                    font-size: 13px;
                    }
           
               }
           }
       }
   }
}